.dashboard {
    padding: 20px;
    font-family: Arial, sans-serif;
    background-color: #f4f4f4;
  }
  
  .dashboard-title {
    text-align: center;
    margin-bottom: 20px;
  }
  
  .cards-container {
    display: flex;
    justify-content: space-around;
  }
  
  .mentee-section, .mentor-section {
    width: 45%;
  }
  
  h2 {
    text-align: center;
    background-color: #007BFF;
    color: white;
    padding: 10px;
    border-radius: 5px;
  }
  